.h-100 {
  height: 100%;
}

.h-50 {
  height: 50%;
}

.h-mx-auto {
  margin-left: auto;
  margin-right: auto;
}

img {
  max-width: 100%;
  height: auto;
}

a {
  display: inline-block;
  outline: none;
}

.checkbox-lg .custom-control-label::before,
.checkbox-lg .custom-control-label::after {
  top: .8rem;
  width: 1.55rem;
  height: 1.55rem;
}

.checkbox-lg .custom-control-label {
  padding-top: 13px;
  padding-left: 6px;
}

.checkbox-xl .custom-control-label::before,
.checkbox-xl .custom-control-label::after {
  top: 1.2rem;
  width: 1.85rem;
  height: 1.85rem;
}

.checkbox-xl .custom-control-label {
  padding-top: 23px;
  padding-left: 10px;
}

.badge {
  margin-right: 10px;
  font-weight: 400;
  // padding: 0.4em 1.4em;
  @include padding-top(0.4rem);
  @include padding-right(0.6rem);
  @include padding-bottom(0.4rem);
  @include padding-left(0.6rem);
  @include font-size(1rem);
}
.list-group-item{
  padding: 0.75rem .35rem;
}

::selection {
  color: #fff;
  background: #fc472c;
}

.navbar-dark .navbar-nav .nav-link:hover, .navbar-dark .navbar-nav .nav-link:focus {
  color: #ffa49d;
}