//首頁區塊
.main_down_group {
  height: 178px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  @include margin-left(4rem);
  @include margin-right(4rem);

  .app_download {
    img {
      width: 100%;
      max-width: 210px;
      display: inline-block;
      // @include margin-left(1rem);
      // @include margin-right(1rem);
    }
  }

  .movie {
    position: relative;
    width: 200px;
    border: 2px solid #fff;

    &::before {
      content: "";
      width: 100%;
      height: 100%;
      position: absolute;
      top: 0;
      left: 0;
      background-color: rgba($color: #fff, $alpha: 0.2);
    }

    &::after {
      display: block;
      content: "";
      position: absolute;
      left: 0;
      top: 0;
      z-index: 3;
      width: 100%;
      height: 100%;
      background: url(http://jbart.joybomb.com.tw/html/bbm3/bbmorfhoficia/images/play_movie.png) no-repeat 50% 50%;
    }
  }
}


//底部下載
.sticky_bottom {
  position: sticky;
  bottom: 0;
  z-index: 1020;
  display: block;
}

.app_download_m {
  padding: 16px 83px;
  // padding: 16px 0px;
}

@media only screen and (orientation: landscape) {
  .app_download {
    display: none;
  }

  .app_download_m {
    display: none;
  }

  .video_group {
    bottom: 13% !important;
  }
}

