.swiper-container {
  width: 600px;
  height: auto;

  @include iPadProP {
    width: 700px;
  }

  @include iPadProL {
    width: 700px;
  }

  @include media-breakpoint-up(xxl) {
    width: 100%;
  }

  @include desktop {
    width: 700px;
  }
}

.swiper-slide {
  text-align: center;
  font-size: 18px;


  /* Center slide text vertically */
  display: -webkit-box;
  display: -ms-flexbox;
  display: -webkit-flex;
  display: flex;
  -webkit-box-pack: center;
  -ms-flex-pack: center;
  -webkit-justify-content: center;
  justify-content: center;
  -webkit-box-align: center;
  -ms-flex-align: center;
  -webkit-align-items: center;
  align-items: center;
}

.swiper-button-prev,
.swiper-button-next {
  z-index: 999999999 !important;
}

.swiper-slide {
  z-index: 10;
}

.flipster__item .flipster__item--current {
