// for Ipad
// and (-webkit-min-device-pixel-ratio: 1.5)
@mixin iPadX {
	@media screen and (min-device-width: 1024px) and (max-device-width: 1366px)  {
		@content
	}
};
// and (-webkit-min-device-pixel-ratio: 1.5)
@mixin iPadP {
	@media only screen and (min-device-width: 768px) and (max-device-width: 1024px) and (orientation: portrait)  {
		@content
	}
};
// and (-webkit-min-device-pixel-ratio: 1.5)
@mixin iPadL {
	@media only screen and (min-device-width: 768px) and (max-device-width: 1024px) and (orientation: landscape){
		@content
	}
};

// for ipadPro 11

@mixin iPadPro11P {
	@media only screen and (min-device-width: 834px) and (max-device-width: 1194px) and (orientation: portrait)  {
		@content
	}
};
// and (-webkit-min-device-pixel-ratio: 1.5)
@mixin iPadPro11L {
	@media only screen and (min-device-width: 834px) and (max-device-width: 1194px) and (orientation: landscape){
		@content
	}
};



// for ipadPro 12
// and (-webkit-min-device-pixel-ratio: 1.5)
@mixin iPadProX {
	@media screen and (min-device-width: 1024px) and (max-device-width: 1366px)  {
		@content
	}
};
// and (-webkit-min-device-pixel-ratio: 1.5)
@mixin iPadProP {
	@media only screen and (min-device-width: 1024px) and (max-device-width: 1366px) and (orientation: portrait) {
		@content
	}
};
// and (-webkit-min-device-pixel-ratio: 1.5)
@mixin iPadProL {
	@media only screen and (min-device-width: 1024px) and (max-device-width: 1366px) and (orientation: landscape){
		@content
	}
};

@mixin iPhoneL {
	@media screen and (max-width:850px) and (orientation: landscape) {
		@content
	}
};

@mixin iPhone5P {
	@media only screen and (max-device-width: 375px)  and (max-device-width:812px) and (orientation: portrait) and (-webkit-min-device-pixel-ratio: 1.5) {
		@content
	}
};


