.close {
  color: #fff;
  opacity: 1;
  position: relative;
  top: 1%;
  right: 5%;
  z-index: 99999;
}

.mobile_menu_img {
  position: fixed;
  top: 2%;
  left: 5%;
  width: 90%;
  height: 30px;
  z-index: 9;
  display: flex;
  justify-content: space-between;

  i {
    background-size: cover;
    background-position: center center;
    width: 30px;
    height: 30px;
    font-size: 30px;
    z-index: 10;
    color: #D9A23A;
  }

  .m_pro_icon {
    width: 35px;
    height: 35px;
  }
}

ul.menu_group {
  background-color: #fff;
  height: 100%;
  position: fixed;
  display: block;
  left: 0;
  padding-left: 0 !important;
  z-index: 999999;
  transform: translateX(-200%);
  transition: transform .5s;
  width: 70%;

  .menu_logo {
    width: 100%;
    text-align: center;
    padding: 40px 20px 20px 20px;
    background-color: #FE0000;

    img {
      width: 60%;
    }
  }
}

ul.menu_group li {
  text-align: center;
  display: block;
  padding: 20px 20px;
  font-weight: 900;
}

ul.menu_group.active {
  transform: translateX(0%);
  overflow: hidden;
  overflow-y: hidden;
  width: 70%;
}
.wrapper .mask{
  display: none;
  overflow: hidden;
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  transition:all .5s;
  background-color:rgba(0, 0, 0, 0);
  opacity: 0;
  z-index: 1020;
}
  
.wrapper.active .mask {
  overflow: hidden;
  display: block;
  transition:all .5s;
  opacity: 1;
  background-color: rgba(0, 0, 0, .6);
  // z-index: 1020;
}

// bootstrap4 menu
.navbar-brand {
  margin-left: 1.5vw;
  margin-right: 6vw;
}

.navbar-logo {
  width: 150px;
}

.navbar-dark .navbar-nav .nav-link {
  color: #ffa49d;
}

.navbar-dark .navbar-nav .nav-link:hover {
  color: #fff;
}

.navbar-dark .navbar-toggler {
  border: none;
}

.bg-nav {
  background: linear-gradient(225deg, rgba(220, 17, 8, 1)35%, rgba(255, 61, 30, 1)60%);
}

.navbar-expand-lg .navbar-nav .nav-link {
  padding-right: 3.5rem;
}

.nav-fb {
  @include font-size(2.5rem);

  i {
    color: #fff;
  }
}

.navbar-collapse {
  z-index: 1000;
  overflow-y: auto;
}

@media (max-width: 1024px) {}

@media (max-width: 995px) {
  .navbar-collapse {
    top: 64px;
  }

  .navbar-nav {
    @include font-size(2.2rem);
    padding: 32px 0 0 24px;
  }
}

@media (max-width: 992px) {
  .navbar-collapse {
    position: fixed;
    top: 64px;
    left: 0;
    padding-left: 15px;
    padding-right: 15px;
    padding-bottom: 15px;
    width: 75%;
    height: 100%;
    background-color: white;
  }

  .navbar-collapse.collapsing {
    left: -75%;
    transition: height 0s ease;
  }

  .navbar-collapse.show {
    left: 0;
    transition: left 300ms ease-in-out;
  }

  .navbar-toggler.collapsed~.navbar-collapse {
    transition: left 500ms ease-in-out;
  }

  .navbar-dark .navbar-nav .nav-link {
    color: #000 !important;
    font-weight: 500;
  }

  .navbar-dark .navbar-nav .nav-link:hover {
    color: #000;
  }

  .navbar-logo {
    width: 100px;
  }
}

@media only screen and (min-device-width: 834px) and (max-device-width: 1194px) and (orientation: portrait) {
  .navbar-collapse {
    top: 80px;
  }
}

@media (max-width: 812px) {
  .navbar-collapse {
    top: 74px;
  }

  .navbar-nav {
    // @include font-size(1rem);
    padding: 32px 0 0 24px;
  }
}


@media only screen and (min-device-width: 768px) and (max-device-width: 1024px) and (orientation: portrait)  {
  .navbar-collapse {
    top: 76px;
  }
}

@media (max-width: 576px) {
  .navbar-collapse {
    top: 66px;
  }
}

.navbar-dark .navbar-toggler {
  border: none;
  outline: none;
}

.navbar-dark .navbar-toggler-icon {
  background-image: none;
}

@keyframes inM {
  50% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(45deg);
  }
}
@keyframes outM {
  50% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(45deg);
  }
}
@keyframes inT {
  0% {
    transform: translateY(0px) rotate(0deg);
  }
  50% {
    transform: translateY(9px) rotate(0deg);
  }
  100% {
    transform: translateY(9px) rotate(135deg);
  }
}

@keyframes outT {
  0% {
    transform: translateY(0px) rotate(0deg);
  }
  50% {
    transform: translateY(9px) rotate(0deg);
  }
  100% {
    transform: translateY(9px) rotate(135deg);
  }
}

@keyframes inBtm {
  0% {
    transform: translateY(0px) rotate(0deg);
  }
  50% {
    transform: translateY(-9px) rotate(0deg);
  }
  100% {
    transform: translateY(-9px) rotate(135deg);
  }
}
@keyframes outBtm {
  0% {
    transform: translateY(0px) rotate(0deg);
  }
  50% {
    transform: translateY(-9px) rotate(0deg);
  }
  100% {
    transform: translateY(-9px) rotate(135deg);
  }
}

.navTrigger i {
  background-color: #fff;
  border-radius: 2px;
  content: '';
  display: block;
  width: 100%;
  height: 4px;
}
.navTrigger i:nth-child(1) {
  -webkit-animation: outT 0.4s backwards;
  animation: outT 0.4s backwards;
  -webkit-animation-direction: reverse;
  animation-direction: reverse;
}
.navTrigger i:nth-child(2) {
  margin: 5px 0;
  -webkit-animation: outM 0.4s backwards;
  animation: outM 0.4s backwards;
  -webkit-animation-direction: reverse;
  animation-direction: reverse;
}
.navTrigger i:nth-child(3) {
  -webkit-animation: outBtm 0.4s backwards;
  animation: outBtm 0.4s backwards;
  -webkit-animation-direction: reverse;
  animation-direction: reverse;
}
.navTrigger.active i:nth-child(1) {
  -webkit-animation: inT 0.4s forwards;
  animation: inT 0.4s forwards;
}
.navTrigger.active i:nth-child(2) {
  -webkit-animation: inM 0.4s forwards;
  animation: inM 0.4s forwards;
}
.navTrigger.active i:nth-child(3) {
  -webkit-animation: inBtm 0.4s forwards;
  animation: inBtm 0.4s forwards;
}

